* {
    font-family: "roboto" sans-serif; }

header {
    position: relative; }

nav {
    height: 50px;
    background-color: #212121; }

.menu {
    display: flex;
    justify-content: space-between;
    padding: 13px 70px;
    margin: 0;
    list-style: none;

    .menu_item {
        border-right: 2px dotted black;
        padding-right: 50px; }

    .menu_link {
        font-size: 15px;
        font-weight: 300;
        text-decoration: none;
        color: #fff; } }


.subheader {
    position: absolute;
    width: 100%;
    min-height: 85px;
    background: rgba(0, 0, 0, 0.6);

    .subheader_logo {
        display: block;
        max-width: 100%;
        margin-top: 16px; }

    .subheader_official {
        margin-top: 6px;
        opacity: .5;
        font-weight: 400;
        font-size: 13px;
        color: #FFFFFF; }

    .subheader_call {
        margin-top: 19px;
        font-weight: 300;
        font-size: 12px;
        text-align: right;
        color: #FFFFFF; }

    .subheader_number {
        margin-top: 8px;
        font-weight: 400;
        font-size: 22px;
        line-height: 12px;
        display: block;
        text-align: right;
        text-decoration: none;
        color: #FFFFFF; }

    .subheader_button {
        margin-top: 19px;
        width: 172px;
        height: 46px;
        font-weight: 400;
        font-size: 14px;
        background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
        box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
        border-radius: 4px;
        border: none;
        text-transform: uppercase;
        color: #fff; } }
.promo {
    padding: 150px 0 135px 0;
    min-height: 800px;
    background: url(../img/uber_bg.jpg) center (center / cover) no-repeat;

    .promo_header {
        font-weight: 700;
        font-size: 60px;
        line-height: 60px;
        text-align: center;
        text-transform: uppercase;
        color: #FFFFFF;
        margin: 0; }

    .promo_subheader {
        font-weight: 700;
        font-size: 36px;
        line-height: 36px;
        text-align: center;
        text-transform: uppercase;
        color: #FFFFFF;
        margin: 0;
        margin-top: 22px; }

    .promo_text {
        margin-top: 16px;
        font-weight: 400;
        font-size: 20px;
        line-height: 24px;
        text-align: center;
        color: #FFFFFF; }

    .promo_button {
        display: block;
        margin: 0 auto;
        margin-top: 115px;
        width: 259px;
        height: 63px;
        font-weight: 400;
        font-size: 14px;
        background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
        box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
        border-radius: 4px;
        border: none;
        text-transform: uppercase;
        color: #fff; } }

.waiting {
  height: 800px;
  background-color: black; }
